const box = document.querySelector('.DnD__draggable');
const DnD = document.querySelector('.DnD');
const DnD__origin = document.querySelector('.DnD__origin');

box.onmousedown = function (event) {
  let shiftX = event.clientX - box.getBoundingClientRect().left;
  let shiftY = event.clientY - box.getBoundingClientRect().top;

  // —Å–æ–∑–¥–∞–µ–º –Ω–æ–≤—ã–π –±–æ–∫—Å –µ—Å–ª–∏ –ø—Ä–æ—à–ª—ã–π –±—ã–ª –≤ –æ—Ä–∏–¥–∂–∏–Ω–µ:
  if (box.parentElement == DnD__origin) {
    let newBox = box.cloneNode();
    DnD__origin.appendChild(newBox);
  }

  box.style.position = 'absolute';
  //box.style.zIndex = 1;
  DnD.append(box);

  moveAt(event.pageX, event.pageY);

  function moveAt(pageX, pageY) {
    box.style.left = pageX - shiftX + 'px';
    box.style.top = pageY - shiftY + 'px';
  }

  let currentDroppable = null;

  function onMouseMove(event) {
    moveAt(event.pageX, event.pageY);

    box.hidden = true;
    let elemBelow = document.elementFromPoint(event.clientX, event.clientY);
    box.hidden = false;

    if (!elemBelow) return;

    let droppableBelow = elemBelow.closest('.droppable');

    if (currentDroppable != droppableBelow) {
      // –º—ã –ª–∏–±–æ –∑–∞–ª–µ—Ç–∞–µ–º –Ω–∞ —Ü–µ–ª—å, –ª–∏–±–æ —É–ª–µ—Ç–∞–µ–º –∏–∑ –Ω–µ—ë
      // –≤–Ω–∏–º–∞–Ω–∏–µ: –æ–±–∞ –∑–Ω–∞—á–µ–Ω–∏—è –º–æ–≥—É—Ç –±—ã—Ç—å null
      //   currentDroppable=null,
      //     –µ—Å–ª–∏ –º—ã –±—ã–ª–∏ –Ω–µ –Ω–∞–¥ droppable –¥–æ —ç—Ç–æ–≥–æ —Å–æ–±—ã—Ç–∏—è (–Ω–∞–ø—Ä–∏–º–µ—Ä, –Ω–∞–¥ –ø—É—Å—Ç—ã–º –ø—Ä–æ—Å—Ç—Ä–∞–Ω—Å—Ç–≤–æ–º)
      //   droppableBelow=null,
      //     –µ—Å–ª–∏ –º—ã –Ω–µ –Ω–∞–¥ droppable –∏–º–µ–Ω–Ω–æ —Å–µ–π—á–∞—Å, –≤–æ –≤—Ä–µ–º—è —ç—Ç–æ–≥–æ —Å–æ–±—ã—Ç–∏—è

      if (currentDroppable) {
        // –ª–æ–≥–∏–∫–∞ –æ–±—Ä–∞–±–æ—Ç–∫–∏ –ø—Ä–æ—Ü–µ—Å—Å–∞ "–≤—ã–ª–µ—Ç–∞" –∏–∑ droppable (—É–¥–∞–ª—è–µ–º –ø–æ–¥—Å–≤–µ—Ç–∫—É)
        leaveDroppable(currentDroppable);
      }
      currentDroppable = droppableBelow;
      if (currentDroppable) {
        // –ª–æ–≥–∏–∫–∞ –æ–±—Ä–∞–±–æ—Ç–∫–∏ –ø—Ä–æ—Ü–µ—Å—Å–∞, –∫–æ–≥–¥–∞ –º—ã "–≤–ª–µ—Ç–∞–µ–º" –≤ —ç–ª–µ–º–µ–Ω—Ç droppable
        enterDroppable(currentDroppable);
      }
    }

    function enterDroppable(elem) {
      elem.style.background = 'pink';
    }

    function leaveDroppable(elem) {
      elem.style.background = '#a4c29c';
    }
  }

  // (3) –ø–µ—Ä–µ–º–µ—â–∞—Ç—å –ø–æ —ç–∫—Ä–∞–Ω—É
  document.addEventListener('mousemove', onMouseMove);

  // (4) –ø–æ–ª–æ–∂–∏—Ç—å –º—è—á, —É–¥–∞–ª–∏—Ç—å –±–æ–ª–µ–µ –Ω–µ–Ω—É–∂–Ω—ã–µ –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ —Å–æ–±—ã—Ç–∏–π
  box.onmouseup = function () {
    document.removeEventListener('mousemove', onMouseMove);
    box.onmouseup = null;

    if (currentDroppable) {
      currentDroppable.append(box);
      currentDroppable.style.background = '#a4c29c';
      if (currentDroppable.classList.contains('dropzone__grid-block')) {
        box.style.left = -1 + 'px';
        box.style.top = -1 + 'px';
      } else if (currentDroppable.classList.contains('dropzone__nogrid')) {
        box.style.left =
          parseInt(box.style.left) -
          currentDroppable.getBoundingClientRect().left +
          'px';
        box.style.top =
          parseInt(box.style.top) -
          currentDroppable.getBoundingClientRect().top +
          'px';
      }
    } else {
      //–ø—Ä–æ–∏–∑–æ—à–µ–ª –≤–∑—Ä—ã–≤
      DnD.removeChild(box);
    }
  };
};
